header {
  width: 100%;
  height: 100px;
  position: fixed;
  top: 0;
  display: flex;
  justify-content: space-between;
  align-items: center;
  color: #f5f5f5;
  background: #151515;
  padding: 0 25%;
}

.logo {
  font-weight: bold;
  font-size: 40px;
}
.links {
  width: 400px;
  display: flex;
  justify-content: space-evenly;
  align-items: center;
}
.links ul {
  width: 280px;
  display: flex;
  justify-content: space-around;
  list-style: none;
}
.links button {
  width: 100px;
  height: 30px;
  background: none;
  color: #f5f5f5;
  border: 1px solid #f5f5f5;
  font-weight: bold;
  cursor: pointer;
}
