.showcase-container {
  min-height: 100vh;
  width: 100%;
  background: #ffffff;
}
.showcase-container-title {
  height: 150px;
  display: flex;
  align-items: center;
  justify-content: center;
}

.cards-container {
  display: flex;
  flex-direction: column;
  margin: auto;
  align-items: center;
  justify-content: center;
  gap: 100px;
}
.flex {
  display: flex;
  gap: 20px;
}

.card {
  width: 400px;
  height: 300px;
}
.card-img {
  width: 400px;
  height: 300px;
  background-image: url("/images/img-1.jpg");
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  border-radius: 5px 5px 0 0;
}

.card-content {
  width: 100%;
  height: 50px;
  background: #f5f5f5;
  border-radius: 0 0 5px 5px;
  color: #000000;
  font-size: 14px;
  display: flex;
  align-items: center;
  justify-content: center;
}
