.hero {
  height: 100vh;
  width: 100%;
  background-image: url("/images/img-9.jpg");
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
  display: flex;
  align-items: center;
  justify-content: center;
  text-align: center;
  overflow-x: hidden;
}
.hero-content h1 {
  font-size: 150px;
  white-space: nowrap;
  color: #fff;
  text-shadow: 1px 1px #000;
}
.hero-content p {
  font-size: 50px;
  text-shadow: 1px 1px #000;
  color: #fff;
}

.hero-content-btn {
  padding: 20px;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 400px;
  gap: 10px;
  margin: auto;
}
.hero-content-btn button {
  width: 150px;
  height: 50px;
  color: #fff;
}

.hero-content-btn button:nth-child(1) {
  border: 1px solid #fff;
  background: rgba(0, 0, 0, 0.3);
  font-weight: bold;
}

.hero-content-btn button:nth-child(2) {
  border: 1px solid #fff;
  background: #fff;
  font-weight: bold;
  color: #000;
}
